import { Component } from '@angular/core';
import { MensagensService } from './mensagens.service';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent {
  mensagens: Conversa[] = [];
  nomeCliente = 'Você';
  nomeBot = 'Trix';
  isLogin = true;
  contextos = [];

  constructor(private mensagemService: MensagensService) { }

  public sendMensagem(campo) {
    const textoPergunta = campo.value;

    if (textoPergunta !== '') {
      this.addPergunta(textoPergunta);
      this.mensagemService.sendMensagem(textoPergunta, this.contextos).subscribe((res) => {
        this.contextos = res.contextos;
        res.mensagem.forEach((el) => {
          this.addResposta(el);
        });
        const element = document.getElementById('chat-mensagens');
        this.scrollTo(element, element.scrollHeight, 500);
      });
    }

    this.limpaCampo(campo);
    campo.focus();
  }

  private addPergunta(textoPergunta) {
    const pergunta = {
      tipo: 'pergunta',
      interlocutor: this.nomeCliente,
      texto: textoPergunta,
      hora: Date.now(),
    };
    this.mensagens.push(pergunta);
  }

  private addResposta(textoResposta) {
    const resposta = {
      tipo: 'resposta',
      interlocutor: this.nomeBot,
      texto: textoResposta,
      hora: Date.now(),
    };
    this.mensagens.push(resposta);
  }

  private limpaCampo(campo) {
    campo.value = '';
  }

  private scrollTo(element, to, duration) {
    var start = element.scrollTop,
        change = to - start,
        currentTime = 0,
        increment = 20;

    const easeInOutQuad = this.easeInOutQuad
    var animateScroll = function(){
        currentTime += increment;
        var val = easeInOutQuad(currentTime, start, change, duration);
        element.scrollTop = val;
        if(currentTime < duration) {
            setTimeout(animateScroll, increment);
        }
    };
    animateScroll();
  }

  //t = current time
  //b = start value
  //c = change in value
  //d = duration
  private easeInOutQuad(t, b, c, d) {
    t /= d/2;
	  if (t < 1) return c/2*t*t + b;
	  t--;
	  return -c/2 * (t*(t-2) - 1) + b;
  };
}

export interface Conversa {
  tipo: string;
  interlocutor: string;
  texto: string;
  hora: number;
}
